




import { Component, Prop, Vue } from 'vue-property-decorator';
@Component
export default class VerifyGia extends Vue {
    public urlBase = 'https://www.gia.edu/report-check?reportno=';
    @Prop(String)
    public giaNumber!: string;
    get url() {
        return this.urlBase + this.giaNumber;
    }
    public created(): void {
    this.$store.state.title = this.$t('cert.verify_Gia');
    }
    public onlond(event: Event): void {
        // todo
      const iframe = (this.$refs.other_iframe as HTMLIFrameElement).contentWindow;
      if (iframe != null && iframe.document) {
          const adown = iframe.document.getElementById('downloadPDF') as HTMLAnchorElement;
          if (adown != null) {
              window.console.log(adown);
          }
      }
    }
}
